<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Loading...</span>
        </div>
    </div>
   <DeptoBase
    :goal="placa.meta_semana"
    :progress="placa.progress"
    :name="placa.nombre"
    :depto="placa.depto"
    :days="placa.dias_laborados"
    :m3="placa.m3_persona"
    :bonus_p="placa.bono_persona"
    :bonus_tot="placa.bono_depto"
    :asis="placa.asistencia"
    :extra="placa.$_extra_m3"
    :bonus_prod="placa.bono_productividad"
    :bonus_goals="placa.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Placa',
    components: {DeptoBase},
    created() {        
        this.$store.dispatch("plants/getInfo", {
            department: 'placa',
            userCode: this.$route.params.user,
            city: 'juarez'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        placa() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>